h1, h2, h3, h4, h5, h6, p,
var,fieldset, form, label, legend
 {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

:root{
    font-size: 18px;
    --fontstack: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

body {
    height: 100vh;
    font-family: var(--fontstack);
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 1fr;
}

.search-bar-container {
    background-color: rgba(0, 0, 255, 0.514);
    display: flex;
    align-items: center;
}

label {
    margin-left: 20px;
    font-weight: bold;
    font-size: 1.2rem;
}

#search-bar {
    height: 30px;
    width: 400px;
    font-size: 1rem;
    margin-left: 5px;
    outline: none;
    border: 1px solid black;
    border-radius: 5px;
}

.weather-data-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.location-header {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 15px;
}

.weather-display-container {
    height: 500px;
    width: 425px;
    border: 2px solid black;
    border-radius: 15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.7);
    display: grid;
    grid-template-rows: 1fr 2fr 1fr 1fr;
    grid-template-columns: 100%;
    grid-gap: 10px;
    align-content: space-around;
    justify-content: center;
}

.condition-header {
    background-color: lightblue;
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 20px;
    border: 2px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.condition-icon {
    justify-self: center;
}

.temp-display {
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
}

.temp-toggle {
    width: 200px;
    height: 40px;
    background-color: white;
    outline: none;
    border: 2px solid black;
    border-radius: 15px;
    justify-self: center;
}

.temp-toggle:hover {
    background-color: #e0e0e0;
    font-weight: bold;
}

.temp-toggle:active {
    transform: scale(1.01);
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;;;;CAIC,SAAS;CACT,UAAU;CACV,SAAS;CACT,eAAe;CACf,aAAa;CACb,wBAAwB;AACzB;;AAEA;IACI,eAAe;IACf,kIAAkI;AACtI;;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,aAAa;IACb,0BAA0B;IAC1B,4BAA4B;AAChC;;AAEA;IACI,wCAAwC;IACxC,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,gBAAgB;IAChB,aAAa;IACb,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,uBAAuB;IACvB,mBAAmB;IACnB,0CAA0C;IAC1C,aAAa;IACb,mCAAmC;IACnC,2BAA2B;IAC3B,cAAc;IACd,2BAA2B;IAC3B,uBAAuB;AAC3B;;AAEA;IACI,2BAA2B;IAC3B,iBAAiB;IACjB,iBAAiB;IACjB,gBAAgB;IAChB,uBAAuB;IACvB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,uBAAuB;IACvB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,oBAAoB;AACxB;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;AACrB;;AAEA;IACI,sBAAsB;AAC1B","sourcesContent":["html, body, div, span,\nh1, h2, h3, h4, h5, h6, p,\nvar,fieldset, form, label, legend\n {\n\tmargin: 0;\n\tpadding: 0;\n\tborder: 0;\n\tfont-size: 100%;\n\tfont: inherit;\n\tvertical-align: baseline;\n}\n\n:root{\n    font-size: 18px;\n    --fontstack: system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n}\n\nbody {\n    height: 100vh;\n    font-family: var(--fontstack);\n    display: grid;\n    grid-template-columns: 1fr;\n    grid-template-rows: 80px 1fr;\n}\n\n.search-bar-container {\n    background-color: rgba(0, 0, 255, 0.514);\n    display: flex;\n    align-items: center;\n}\n\nlabel {\n    margin-left: 20px;\n    font-weight: bold;\n    font-size: 1.2rem;\n}\n\n#search-bar {\n    height: 30px;\n    width: 400px;\n    font-size: 1rem;\n    margin-left: 5px;\n    outline: none;\n    border: 1px solid black;\n    border-radius: 5px;\n}\n\n.weather